<template>
  <div id="app">
    <div class="login"><button @click="add">增加item</button><button @click="remove">删除item</button></div>
    
    <div>
      <img width="120" height="120" src="https://user-images.githubusercontent.com/6414178/45662644-3a441000-bb35-11e8-8e72-e6ed618a733d.png">
    </div>
    
    <div class="title">
      Vue Metro Tile
    </div>
    <div class="subtitle">
      *click to animate or drag to rearrange
    </div>

    <div class="grid-layout-container margin-right">
      <grid-layout
                  :layout="layout"
                  :col-num="12"
                  :colWidth="BASE_LEN"
                  :row-height="BASE_LEN"
                  :is-draggable="true"
                  :is-resizable="true"
                  :is-mirrored="false"
                  :vertical-compact="true"
                  :margin="[0, 0]"
                  :use-css-transforms="true"
                  @layout-updated="layoutUpdatedEvent"
      >
        <grid-item v-for="(item,index) in layout"
                   :x="item.x"
                   :y="item.y"
                   :w="item.w"
                   :h="item.h"
                   :i="item.i"
                   :key="item.i" 
                 >
          <div class="container" @mousedown='onmousedown($event)' @mouseup ="onmouseup($event,item,index)">
            <MetroTile 
                :width="item.w*BASE_LEN-6"
                :height="item.h*BASE_LEN-6"
                :rotateX="item.rotateX"
                :faceStyle="{background:item['background-color']}"  :data-type='item.moduleid'>
             
              <template v-if="item.type == 1">
                <div slot="front">
                   <img :src="item.img" alt="" class="icon">
                    <div class="tile-label" v-if="item.name">
                     {{item.name}}
                    </div>
                </div>
              </template>
              <!-- 日期类型 -->
              <!-- <template v-else-if="item.type == 2">
                <div slot="front">
                    <div class="calendar">
                      <div class="calendar-week-day">
                        {{weekDay}}
                      </div>
                      <div class="calendar-month-day">
                        {{monthDay}}
                      </div>
                    </div>
                </div>
              </template> -->
              <template v-else-if="item.type === 2">
                <div slot="front" data-type='0' :style='{height:"100%",background:item.item[0]["background-color"]}'>
                    <div class="tile-label">
                     {{item.item[0].name}}
                    </div>
                </div>
                <div slot="top" data-type='1' :style='{height:"100%",background:item.item[1]["background-color"]}'>
                    <div class="tile-label">
                       {{item.item[1].name}}
                    </div>
                </div>
                <div slot="bottom" data-type='2' :style='{height:"100%",background:item.item[2]["background-color"]}'>
                     {{item.item[2].name}}
                </div>
              </template>
            </MetroTile>
          </div>
        </grid-item>
      </grid-layout>
    </div>
  <div class="menu" ref='menu' tabindex='-1' @blur='menuBlur' :style='menuStyle' @click.stop="menuClick">
    <div class='menu-item' :class='{selected:menuSelectIndex==1}' data-type='1'>中</div>
    <div class='menu-item' :class='{selected:menuSelectIndex==2}' data-type='2'>宽</div>
    <div class='menu-item' :class='{selected:menuSelectIndex==3}' data-type='3'>大</div>
    <div class='menu-item' data-type='remove'>删除</div>
  </div>
    <!-- <div class="grid-layout-container">
      <grid-layout
                  :layout="layoutRight"
                  :col-num="6"
                  :colWidth="BASE_LEN"
                  :row-height="BASE_LEN"
                  :is-draggable="true"
                  :is-resizable="false"
                  :is-mirrored="false"
                  :vertical-compact="true"
                  :margin="[0, 0]"
                  :use-css-transforms="true"
                   @layout-updated="layoutRightUpdatedEvent"
                 
      >
        <grid-item v-for="item in layoutRight"
                   :x="item.x"
                   :y="item.y"
                   :w="item.w"
                   :h="item.h"
                   :i="item.i"
                  
                   :key="item.i">
          <div class="container"  @click="clickFn('right',item.i)">
            <MetroTile 
                :width="item.width"
                :height="item.height"
                :length="item.length"
                :rotateX="item.rotateX"
                :faceStyle="item.faceStyle"
                :frontStyle="item.frontStyle"
                :topStyle="item.topStyle"
                :backStyle="item.backStyle"
                :bottomStyle="item.bottomStyle"
                :perspective="item.perspective"
                v-on="item.i === '5' ? {click: redirect} : {}">
             
              <template v-if="item.type == 1">

                <div slot="front">
                  <div class="tile-label" v-if="item.text">
                   {{item.text}}
                  </div>
                  <img :src="item.url" alt="" class="icon">
                </div>
              </template>
              <template v-else-if="item.type ==2">
                <div slot="front">
                  <div class="tile-label">
                    {{item.text}}
                  </div>
                  <img :src="item.url" class="image" />
                </div>
                <div slot="top">
                  <div class="tile-label">
                   {{item.text1}}
                  </div>
                  <img :src="item.url1" class="image" />
                </div>
              </template>
            </MetroTile>
          </div>
        </grid-item>
      </grid-layout>
    </div> -->
  </div>
</template>

<script>


import MetroTile from './components/MetroTile.vue'
import VueGridLayout from 'vue-grid-layout';


const BASE_LEN = 65;
// const MARGIN = data1.MARGIN;
const REPO_URL = 'https://github.com/yuanfux/vue-metro-tile';

export default {
  name: 'app',
  components: {
    MetroTile,
    VueGridLayout,
   
  },
  data: () => ({
   
    BASE_LEN,
    layout:[],
    menuStyle:{
      top:0,
      left:0,
      display:'none'
    },
    itemSelectIndex:0,
    menuSelectIndex:0,
    weekDay: (() => {
      const weekDayMap = {
        0: 'Sunday',
        1: 'Monday',
        2: 'Tuesday',
        3: 'Wednesday',
        4: 'Thursday',
        5: 'Friday',
        6: 'Saturday'
      }
      return weekDayMap[new Date().getDay()];
    })(),
    monthDay: (() => {
      return new Date().getDate();
    })(),
    rotateArray: [90, 90,90,90,-90,-90,-90,-90],  
    rotateIndex:0,
    site:null,
    interval:null,
    rotateItemIndex:[]
  }),
  
   mounted() {
     this.getData()
    //  禁止浏览器默认右键菜单
     document.oncontextmenu=function(){
       return false
     }
    
    
  },
  methods: {
    add(){
      this.itemChange(true)
    },
    remove(){
      this.itemChange(false)
    },
    itemChange(add){
      // 数据模板
      let itemTemplate = {
          "moduleid": "unit",
          "name": "机构管理",
          "img": "unit.png",
          "icon": "fa fa-home",
          "jsaction": "alert(2)",
          "width": Math.random()<0.5 && 4 || 2,
          "height": 2,
          "background-color": "#228b22"
      };
     
      if(add){
        this.layout = this.dataFormat([...this.layout,itemTemplate]);
      }else{
        let i = ~~(Math.random()*this.layout.length);
        this.layout.splice(i,1)
      }
      this.saveData(this.layout)
    
    },
    
    redirect() {
      window.open(REPO_URL, '_blank');
      // window.location.href = REPO_URL;
    },
    mouseRightClick(e,item,index){
    
        // 获取鼠标坐标
      let mouseX = e.clientX;
      let mouseY = e.clientY;
      //获取可视区宽度,高度
      let winWidth = document.documentElement.clientWidth || document.body.clientWidth;
      let winHeight = document.documentElement.clientHeight || document.body.clientHeight;
      let menu = this.$refs.menu;
      // 判断边界值，防止菜单栏溢出可视窗口
      if (mouseX >= (winWidth - menu.offsetWidth)) {
        mouseX = winWidth - menu.offsetWidth;
      } else {
        mouseX = mouseX
      }
      if (mouseY > winHeight - menu.offsetHeight) {
        mouseY = winHeight - menu.offsetHeight;
      } else {
        mouseY = mouseY;
      }
      console.log()
      let menuSelectIndex = -1;
      if(item.w==2&&item.h==2){
        menuSelectIndex = 1
      }else if(item.w==4&&item.h==4){
         menuSelectIndex = 3
      }else if(item.w==4&&item.h==2){
        menuSelectIndex = 2
      }
      this.menuSelectIndex = menuSelectIndex;
      this.itemSelectIndex = index;
      this.menuStyle={
        left:mouseX+'px',
        top:mouseY+'px',
        display:'block',
        
      }
    },
    menuBlur(){
      this.menuStyle.display='none';
    },
    menuClick(e){
      let type = e.target.dataset.type;
      if(!type || type == this.menuSelectIndex){
        this.menuStyle.display = 'none';
        return false;
      }
      let size = [[2,2],[4,2],[4,4]];
      let index = this.itemSelectIndex;
      if(type=='remove'){
        this.layout.splice(index,1)
        // 重新获取旋转元素index
        let arr = [];
        this.layout.forEach((item,index)=>{
          if(item.item){
            arr.push(index)
          }
        })
        if(arr.length>0){
          this.rotateItemIndex = arr;
        }else{
          this.interval && clearInterval(this.interval)
        }
      }else{
        this.layout.splice(index,1,{...this.layout[index],w:size[type-1][0],h:size[type-1][1]})
      }
      this.menuStyle.display = 'none';
      this.saveData(this.layout)
      
    },
    onmousedown(e){
      this.site = {
        x:e.pageX,
        y:e.pageY
      };
    },
    onmouseup(e,item,index){
      if(e.button==2){

        this.mouseRightClick(e,item,index)
        return false;
      }
      let x = this.site.x - e.pageX;
      let y = this.site.y - e.pageY;
      
      // 点击事件时此差值为0,大于10为容错，若不精准可缩小此值
      if(Math.abs(x)+Math.abs(y)>10){
        return false;
      }
      let jsaction = item.jsaction;
      if(item.item){
        let type = e.target.dataset.type;
        jsaction =  item.item[type] && item.item[type].jsaction || ''
      }
      if(jsaction){
        eval(jsaction)
      }
      
    },
    layoutUpdatedEvent(newLayout){
      this.saveData(newLayout)
    },
    getData() {
      let layout = localStorage.getItem('layout');
      // 缓存取值
      if(layout){
          this.layout = JSON.parse(layout);
          this.layout.forEach((item,index)=>{
            if(item.item){
              this.rotateItemIndex .push(index)
            }
          })
          this.registerTimer();
          return false;
      }
      // 服务器取值
      this.http.get('https://www.fastmock.site/mock/4cd04d931e195f79858f5993ff4a1c38/metro/api/admin').then(res => {
      //  数据格式化
       let data = this.dataFormat(res.data.rows);
        this.layout = data;
        // 注册旋转定时器
        this.registerTimer()
      }, err => {
            window.console.log("error",err);
      });
    },
    saveData(newLayout){
      localStorage.setItem('layout',JSON.stringify(newLayout));
    },
    dataFormat(arr){
      // 数据格式化
      let row = 12;
      let col = 0,itemCol=0;
      // 数据过滤及属性初始化
      let oldArr = [];
      let newArr = arr.filter((item,index)=>{
        item.i = item.id&& item.id || ~~(Math.random()*10000+10)
        item.rotateX = 0;
        item.type = item.item && (this.rotateItemIndex.push(index),2)  || 1;
        if(item.x!==undefined){
          oldArr.push(item)
          return false;
        }
        return true
      })
      if(oldArr.length>0){
        let lastItem = oldArr.sort((x,y)=>y.y-x.y).filter(item=>item.y==oldArr[0].y).sort((x,y)=>y.x-x.x)[0];
        console.log(lastItem)
        col = lastItem.y;
        row = 12-lastItem.x-lastItem.width;
        itemCol = lastItem.height;
        window.console.log(row,col,itemCol)
      }
     
      newArr.map(item=>{
        let w = item.width;
        if(w <= row){
          item.height>itemCol && (itemCol = item.height);
          item.x = 12-row;
        }else{
          col+=itemCol;
          item.x = 0;
          row = 12;
        }
        row-=w;
        item.y = col;
        
        item.w = item.width;
        item.h = item.height;
       
        return item;
      })
      return arr;
    },
    setRotateX(layoutItem, rotateIndexName, rotateArray) {
      layoutItem.rotateX += rotateArray[this[rotateIndexName] % rotateArray.length];
      this[rotateIndexName] += 1;
    },
    registerTimer(){
      // // 注册定时器 
        this.interval = setInterval(() => {
         
            this.rotateItemIndex.forEach(item=>{
               console.log(item)
              this.setRotateX(this.layout[item], 'rotateIndex', this.rotateArray);
            })
        }, 3000);
    },
   
   
  },
 
  beforeDestroy() {
    this.interval && clearInterval(this.interval)
  }
}
</script>

<style>
#app {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100vw;
  height: 100vh;
  text-align: center;
}

body {
  background: radial-gradient(circle, rgba(61,82,108,1) 0%, rgba(29,50,75,1) 100%);
}

.title {
  font-family: "Segoe UI", Frutiger, "Frutiger Linotype", "Dejavu Sans", "Helvetica Neue", Arial, sans-serif;
  font-size: 20px;
  font-weight: 500;
  padding: 0 0 10px 0;
  color: #fff;
}

.subtitle {
  font-family: "Segoe UI", Frutiger, "Frutiger Linotype", "Dejavu Sans", "Helvetica Neue", Arial, sans-serif;
  font-size: 17px;
  font-weight: 500;
  padding-bottom: 30px;
  color: #fff;
}

.grid-layout-container {
  display: inline-block;
  width: 780px;
  height: 100%;
}

.margin-right {
  margin-right: 17px
}

.container {
  margin: 3px;
  width: calc(100% - 6px);
  height: calc(100% - 6px);
}

.icon {
  position: absolute;
