  position: absolute;
  top: 35px;
  left: 65px;
}

.tile-label {
  font-family: "Segoe UI", Frutiger, "Frutiger Linotype", "Dejavu Sans", "Helvetica Neue", Arial, sans-serif;
  font-size: 14px;
  font-weight: 500;
  color: #fff;
  position: absolute;
  left: 8px;
  bottom: 5px;
}

.vue-grid-item.vue-grid-placeholder {
  background-color: transparent !important;
}

.calendar {
  width: 100%;
  height: 100%;
  position: absolute;
  text-align: center;
  font-family: "Segoe UI", Frutiger, "Frutiger Linotype", "Dejavu Sans", "Helvetica Neue", Arial, sans-serif;
  color: #fff;
}
.calendar .calendar-week-day {
  margin-top: 20px;
  font-size: 14px;
  font-weight: 500;
}

.calendar .calendar-month-day {
  margin-top: 15px;
  font-size: 30px;
  transform: scale(1.75);
}

.login{
  position: fixed;
  right: 20px;
  top: 10px;
  padding: 5px 20px;
  color: #fff;
  font-size: 14px;
}
.login button{
  margin-right: 10px;
}

.menu {
  display: none;
  width: 100px;
  position: absolute;
  padding: 0;
  background:#fff;
  border: 1px solid #ccc;
  text-align: center;
  box-shadow: 2px 2px 8px 1px rgba(0, 0, 0, .2);
  outline: none;
      
}
.menu .menu-item {
  list-style: none;
  width: 100%;
}
.menu .menu-item {
  display: inline-block;
  text-decoration: none;
  color: #555;
  width: 100%;
  padding: 10px 0;
  text-align: center;
  cursor: pointer;
}
.menu .menu-item:first-of-type {
  border-radius: 5px 5px 0 0;
}
.menu .menu-item.selected{
  background: #eee;
  color: #CC1A1A;
}
</style>
